import React, { Fragment, useContext, useEffect, useState,  } from 'react'
import { AuthContext } from '../../context/AuthContext'
import { FoodAddContext } from '../../context/FoodAddContext'
import { MealContext } from '../../context/MealContext'
import { UiContext } from '../../context/UiContext'
import { actionTypes } from '../../context/UiContextReducer'
import {ReactComponent as CloseIcon} from '../../assets/close.svg'
import { CancelDeleteButton, ConfirmDeleteButton, DeleteModalButtonContainer } from '../deleteButtton/DeleteModal.styles'
import { MealTitleInput } from '../mealTitle/MealTitle.styles'
import GoBackIcon from '../ui/icon/GoBackIcon'

import Modal from '../ui/modal/Modal'
import {  CloseMealAddingIconContainer, ConfirmButtonContainer, ConfirmEditButton, EditModalContainer,  MainNutritionContainer, MealAddingFormContainer, MealAddingHeaderContainer, MealAddingTitle,  Nutrition, NutritionalInformationContainer, NutritionalInformationTitle, NutritionContainer, NutritionSize, SearvingText, ServingAndCalorieCountContainer, ServingInput, ServingInputContainer, TotalCalories, TotalCaloriesText } from './EditMealModal.styles'
import { CaloriesTextContainer,ServingCalorieCount } from '../finishAddingMeal/MealAdding.Styles'
import LoadingSpinner from '../ui/loading/LoadingSpinner'

const EditMealModal = () => {

  const [servingInput, setServingInput] = useState(1)

    const {showEditModal,UiToggleHelper} = useContext(UiContext)
    const {updateMealHandler} = useContext(FoodAddContext)
    const {selectedId,updateDatabase,mealInfo} = useContext(MealContext)

    const {userId} = useContext(AuthContext)
 



    

    const cancelEdithandler =  () => {

     UiToggleHelper(actionTypes.SHOW_EDIT_MODAL, showEditModal)
  }

        const servingInputHandler = (event) => {
          setServingInput(event.target.value)
      }
      
      const EditItemHandler = async(event,selectedMeal) => {
        event.preventDefault()

     await updateMealHandler(selectedId,userId,selectedMeal,servingInput).then(() => {
      updateDatabase(userId)
      UiToggleHelper(actionTypes.SHOW_EDIT_MODAL, showEditModal)
     })
           

              }
   



let mySelectedMealToEdit;

if(showEditModal && !mealInfo ){
mySelectedMealToEdit = <LoadingSpinner/>
}
     else if( mealInfo){

      mySelectedMealToEdit = <EditModalContainer 
      as={Modal}  
      close={cancelEdithandler} 
  
       show={showEditModal}>
<MealAddingHeaderContainer>

  
           <MealAddingTitle>{ mealInfo ? mealInfo.mealInfo.description : 'Meal'}</MealAddingTitle>
           <CloseMealAddingIconContainer>
           <CloseIcon onClick={cancelEdithandler}/>
           </CloseMealAddingIconContainer>
          
          
       </MealAddingHeaderContainer>

       <MealAddingFormContainer  onSubmit={(event) => EditItemHandler(event,mealInfo)}>
           <ServingAndCalorieCountContainer>
           <ServingInputContainer>

<ServingInput required type='number' value={servingInput}  onChange={servingInputHandler} placeholder={'serving size'}/>
<SearvingText>Serving</SearvingText>
</ServingInputContainer>
<ServingCalorieCount>
 <CaloriesTextContainer>
     <TotalCaloriesText>Weight: </TotalCaloriesText> <TotalCalories>{ parseInt(mealInfo.totalWeight)  * servingInput }<span> g</span></TotalCalories>  
     </CaloriesTextContainer>
 
     <CaloriesTextContainer>
     <TotalCaloriesText>Total Calories: </TotalCaloriesText> <TotalCalories>{ mealInfo.totalCalories  * servingInput }<span> kcal</span></TotalCalories> 
     </CaloriesTextContainer>
 
   
            
            </ServingCalorieCount>
           </ServingAndCalorieCountContainer>
           

           {mealInfo &&  
           <NutritionalInformationContainer>
               <NutritionalInformationTitle>
                   Nutritional  Information
               </NutritionalInformationTitle>
              
              {  mealInfo.mealInfo.foodNutrients.map(nutrient => {
          return(
              <NutritionContainer key={nutrient.nutrientId}>
              <MainNutritionContainer>
              <Nutrition>{nutrient.nutrientName}</Nutrition>
             <NutritionSize>{nutrient.value} {nutrient.unitName}</NutritionSize>
              </MainNutritionContainer>
     
       
  
          </NutritionContainer>
          )
      })}
 
             
           </NutritionalInformationContainer>
        
           }



<ConfirmButtonContainer>
           
           <ConfirmEditButton type='submit' >Confirm</ConfirmEditButton>
       </ConfirmButtonContainer>
           </MealAddingFormContainer>
      
      </EditModalContainer>



}
return mySelectedMealToEdit
}
export default EditMealModal